interface MenuItemProps {
    children: string;
    path: string;
    exact?: boolean;
}

const MenuItem: React.FC<MenuItemProps> = ({ children, path, exact }) => {
    const match = useRouteMatch({
        path,
        exact,
    });

    return (
        <div className={`aside-menu-item ${match ? 'active' : ''}`}>
            <Link className={`aside-menu-item-link`} to={path}>
                {children}
            </Link>
        </div>
    );
};

